@use 'base/variables' as v;
@use 'base/mixins' as m;

.interactivo{
    margin: 10rem auto;
    

    @include m.escritorioXL{
        position: relative;
    }

    picture{
        @include m.escritorioXL{
            width: 65%;
        }
    }

    .texto{

        text-align: center;

        h2{
            margin: 2rem 0;
            text-transform: uppercase;
            font-size: 3.8rem;

            @include m.escritorioXL {
                font-size: 4.8rem
            }
        }

        p{
            font-size: 1.8rem;
            font-family: v.$Josefin;
            line-height: 2;
        }

        

        @include m.escritorioXL{
            display: inline-block;
            width: 55%;
            position: absolute;
            bottom: 0;
            right: 0;
            background: v.$blanco;
            padding: 7rem 7rem 0 7rem;
            text-align: left;
        }
        
        
    }

}