html {
  font-size: 62.5%;
}

body {
  font-family: "Alata", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 90%;
  max-width: 1440px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img, picture {
  width: 100%;
  height: 100%;
  display: block;
}

h1, h2 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
}

p {
  font-family: "Josefin Sans", sans-serif;
}

.header {
  width: 100%;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.header .titulo {
  margin: 0 auto;
  position: absolute;
  top: 35%;
  display: block;
  font-size: 3.8rem;
  width: 90%;
  color: #ffffff;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  padding: 3rem;
}
@media (min-width: 480px) {
  .header .titulo {
    width: 70%;
    font-size: 4.8rem;
  }
}
@media (min-width: 768px) {
  .header .titulo {
    width: 55rem;
    font-size: 6.2rem;
  }
}
@media (min-width: 992px) {
  .header .titulo {
    width: 70%;
    font-size: 8.6rem;
  }
}
@media (min-width: 1200px) {
  .header .titulo {
    width: 70%;
    font-size: 9.8rem;
  }
}
@media (min-width: 1400px) {
  .header .titulo {
    width: 55%;
    font-size: 11rem;
  }
}

.webp .header {
  background-image: url("../img/desktop/image-hero.webp");
}

.no-webp .header {
  background-image: url("../img/desktop/image-hero.jpg");
}

.navegacion {
  padding-top: 2rem;
  height: 8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navegacion .menu {
  width: 4rem;
  z-index: 6;
}
.navegacion .menu:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .navegacion .menu {
    display: none;
  }
}
.navegacion .logo {
  width: 20rem;
  height: auto;
  z-index: 7;
}
.navegacion .enlaces-header {
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  width: 100%;
  height: 100vh;
  background: #000000;
  z-index: 5;
  flex-direction: column;
  justify-content: space-around;
  padding: 15rem 5%;
  clip-path: circle(0% at 100% 0);
  transition: all 0.5s ease;
}
.navegacion .enlaces-header a {
  font-family: "Josefin Sans", sans-serif;
  font-size: 4.2rem;
  color: #ffffff;
  position: relative;
  padding-bottom: 0.5rem;
}
.navegacion .enlaces-header a:before {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #ffffff;
  transition: all 0.3s ease;
  position: absolute;
  left: 0;
  bottom: 0;
}
.navegacion .enlaces-header a:hover:before {
  width: 50%;
}
@media (min-width: 768px) {
  .navegacion .enlaces-header a {
    font-size: 1.6rem;
    display: inline-block;
    margin-right: 2rem;
    position: relative;
    padding-bottom: 1rem;
    font-family: "Alata", sans-serif;
  }
  .navegacion .enlaces-header a:before {
    content: "";
    display: block;
    width: 0;
    height: 2px;
    background: #ffffff;
    transition: all 0.3s ease;
    position: absolute;
    left: 50%;
    bottom: 0;
  }
  .navegacion .enlaces-header a:hover:before {
    left: 0;
    width: 100%;
  }
}
@media (min-width: 768px) {
  .navegacion .enlaces-header {
    background: none;
    clip-path: none;
    width: auto;
    height: auto;
    display: inline-block;
    padding: 0;
    position: relative;
  }
}
.navegacion .abrir {
  clip-path: circle(141.2% at 100% 0);
}

.interactivo {
  margin: 10rem auto;
}
@media (min-width: 1200px) {
  .interactivo {
    position: relative;
  }
}
@media (min-width: 1200px) {
  .interactivo picture {
    width: 65%;
  }
}
.interactivo .texto {
  text-align: center;
}
.interactivo .texto h2 {
  margin: 2rem 0;
  text-transform: uppercase;
  font-size: 3.8rem;
}
@media (min-width: 1200px) {
  .interactivo .texto h2 {
    font-size: 4.8rem;
  }
}
.interactivo .texto p {
  font-size: 1.8rem;
  font-family: "Josefin Sans", sans-serif;
  line-height: 2;
}
@media (min-width: 1200px) {
  .interactivo .texto {
    display: inline-block;
    width: 55%;
    position: absolute;
    bottom: 0;
    right: 0;
    background: #ffffff;
    padding: 7rem 7rem 0 7rem;
    text-align: left;
  }
}

.nosotros {
  margin-bottom: 5rem;
}

.encabezado-nosotros {
  text-align: center;
  margin-bottom: 6rem;
}
@media (min-width: 768px) {
  .encabezado-nosotros {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 10rem;
  }
}
.encabezado-nosotros h2 {
  font-size: 4.2rem;
  text-transform: uppercase;
}
.encabezado-nosotros .boton {
  display: none;
}
@media (min-width: 768px) {
  .encabezado-nosotros .boton {
    font-family: "Josefin Sans", sans-serif;
    text-transform: uppercase;
    background: #ffffff;
    color: #000000;
    font-weight: 400;
    text-decoration: none;
    padding: 1.6rem 3.2rem;
    text-align: center;
    display: block;
    border: 1px solid #000000;
    margin: 0;
  }
  .encabezado-nosotros .boton:hover {
    background: black;
    color: white;
    cursor: pointer;
  }
}

.tarjetas-escritorio {
  display: none;
}
@media (min-width: 768px) {
  .tarjetas-escritorio {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.tarjetas-escritorio .contenedor-imagen {
  object-fit: cover;
}

.tarjetas-mobil .contenedor-imagen {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .tarjetas-mobil {
    display: none;
  }
}

.contenedor-imagen {
  position: relative;
}
.contenedor-imagen .overlay {
  color: #ffffff;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(0deg, #FFFFFF00 0%, #00000059 0%);
  display: flex;
  align-items: flex-end;
}
.contenedor-imagen .overlay p {
  font-size: 2.4rem;
  text-transform: uppercase;
  width: 25rem;
  padding: 1rem 8rem 2rem 2rem;
}
@media (min-width: 1200px) {
  .contenedor-imagen .overlay p {
    width: 30rem;
    font-size: 3.2rem;
  }
}
.contenedor-imagen .overlay:hover {
  background: #FFFFFFBF;
}
.contenedor-imagen .overlay:hover p {
  color: #000000;
}

.footer {
  background: #000000;
}
.footer .contenedor {
  display: flex;
  padding: 2rem 0;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer .contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer .contenedor .footer-navegacion {
  height: auto;
  padding: 0;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer .contenedor .footer-navegacion {
    height: 8rem;
    justify-content: space-between;
    align-items: flex-start;
  }
}
.footer .contenedor .contacto {
  height: auto;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .contenedor .contacto {
    height: 8rem;
    align-items: flex-end;
    justify-content: space-between;
  }
}
.footer .contenedor .logo {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .footer .contenedor .logo {
    margin-bottom: 0;
  }
}

.enlaces-footer a {
  width: 100%;
  text-align: center;
  display: block;
  margin-bottom: 3rem;
  margin-right: 2rem;
  color: #ffffff;
  position: relative;
  padding-bottom: 1rem;
}
.enlaces-footer a:last-of-type {
  margin-right: 0;
}
.enlaces-footer a:before {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #ffffff;
  transition: all 0.3s ease;
  position: absolute;
  left: 50%;
  bottom: 0;
}
.enlaces-footer a:hover:before {
  left: 25%;
  width: 50%;
}
@media (min-width: 768px) {
  .enlaces-footer a {
    width: auto;
    display: inline-block;
    margin-bottom: 0;
    text-align: left;
  }
}

.contacto .redes a {
  color: #ffffff;
  margin-right: 3rem;
  font-size: 2.4rem;
  position: relative;
  padding-bottom: 1rem;
}
.contacto .redes a:before {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #ffffff;
  transition: all 0.3s ease;
  position: absolute;
  left: 50%;
  bottom: 0;
}
.contacto .redes a:hover:before {
  left: 0;
  width: 100%;
}
.contacto .redes a:last-of-type {
  margin-right: 0;
}

.copyright {
  margin-top: 3rem;
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .copyright {
    margin-top: 0;
  }
}