@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background: v.$negro;
    
    .contenedor{
        display: flex;
        padding: 2rem 0;
        flex-direction: column;

        @include m.tablet{
            flex-direction: row;
            justify-content: space-between;
        }

        .footer-navegacion{
            height: auto;
            padding: 0;
            flex-direction: column;
            

            @include m.tablet{
                height: 8rem;
                justify-content: space-between;
                align-items: flex-start;
            }


        }
    
        .contacto{
            height: auto;
            color: v.$blanco;
            display: flex;
            flex-direction: column;
            align-items: center;
            
            @include m.tablet{
                height: 8rem;
                align-items: flex-end;
                justify-content: space-between;
            }
        }

        .logo{
            margin-bottom: 3rem;

            @include m.tablet{
                margin-bottom: 0;
            }
        }
    }

    
}

.enlaces-footer{
    a{
        width: 100%;
        text-align: center;
        display: block;
        margin-bottom: 3rem;
        margin-right: 2rem;
        color: v.$blanco;

        &:last-of-type{
            margin-right: 0;
        }

        @include m.borde(v.$blanco, 50%, 25%);

        @include m.tablet{
            width: auto;
            display: inline-block;
            margin-bottom: 0;
            text-align: left;

            
        }
    }
}

.contacto{
    .redes{
        a{
            color: v.$blanco;
            margin-right: 3rem;
            font-size: 2.4rem;

            @include m.borde(v.$blanco, 100%, 0);
            

            &:last-of-type{
                margin-right: 0;
            }
        }
    }
}

.copyright{
    margin-top: 3rem;
    padding-bottom: 1rem;

    @include m.tablet {
        margin-top: 0;
    }
}