@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros{
    margin-bottom: 5rem;
}

.encabezado-nosotros{
    text-align: center;
    margin-bottom: 6rem;

    @include m.tablet{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 10rem;
    }

    h2{
        font-size: 4.2rem;
        text-transform: uppercase;
    }

    .boton{
        display: none;

        @include m.tablet{
            font-family: v.$Josefin;
            text-transform: uppercase;
            @include m.boton(v.$blanco, block, v.$regular, v.$negro )
        }
        
    }
}

.tarjetas-escritorio{
    
    display: none;

    @include m.tablet{
        @include m.grid(4, 2rem);
    }

    .contenedor-imagen{
        object-fit: cover;
    }
}

.tarjetas-mobil{
    
    .contenedor-imagen{
        margin-bottom: 1rem;
    }
    
    @include m.tablet{
        display: none;
    }

}

.contenedor-imagen{
    position: relative;
    

    .overlay{
        color: v.$blanco;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: v.$overlay;
        display: flex;
        align-items: flex-end;

        p{
            font-size: 2.4rem;
            text-transform: uppercase;
            width: 25rem;
            padding: 1rem 8rem 2rem 2rem;
        
            @include m.escritorioXL{
                width: 30rem;
                font-size: 3.2rem;
            }
        }

        &:hover{
            background: v.$overlayH;

            p{
                color: v.$negro;
            }
        }

    }    
}