@use 'variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin escritorio {
    @media (min-width: v.$desktop) {
        @content;
    }    
}

@mixin escritorioXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }    
}

@mixin escritorioXLL {
    @media (min-width: v.$desktopXLL) {
        @content;
    }    
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}



@mixin boton($bgColor, $display, $grosor, $cletra) {
    background: $bgColor;
    color: $cletra;
    font-weight: $grosor;
    text-decoration: none;
    padding: 1.6rem 3.2rem;
    text-align: center;
    display: $display;
    border: 1px solid v.$negro;
    margin: 0;

    &:hover{
        background: darken($bgColor, 100);
        color: lighten($cletra, 100);
        cursor: pointer;
    }
}

@mixin borde($bg, $tama単o, $destino) {
    position: relative;
    padding-bottom: 1rem;

    &:before{
        content: "";
        display: block;
        width: 0;
        height: 2px;
        background: $bg;
        transition: all .3s ease;
        position: absolute;
        left: 50%;
        bottom: 0;
    }

    &:hover:before{
        left: $destino;
        width: $tama単o;
    }
}

@mixin bordeMenu($bg, $tama単o) {
    position: relative;
    padding-bottom: .5rem;

    &:before{
        content: "";
        display: block;
        width: 0;
        height: 2px;
        background: $bg;
        transition: all .3s ease;
        position: absolute;
        left: 0;
        bottom: 0;
    }

    &:hover:before{
        width: $tama単o;
    }
}