@use 'variables' as v;
@use 'mixins' as m;


html{
    font-size: 62.5%;
}

body{
    font-family: v.$Alata;
    font-size: 1.6rem;
}

.contenedor{
    width: 90%;
    max-width: 1440px;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

img, picture{
    width: 100%;
    height: 100%;
    display: block;
}

h1, h2{
    font-family: v.$Josefin;
    font-weight: v.$delgada;
}

p{
    font-family: v.$Josefin;
}

