@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    width: 100%;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    

    .titulo{
        margin: 0 auto;
        position: absolute;
        top: 35%;
        display: block;
        font-size: 3.8rem;
        width: 90%;
        color: v.$blanco;
        text-transform: uppercase;
        border: 1px solid v.$blanco;
        padding: 3rem;

        @include m.telefono{
            width: 70%;
            font-size: 4.8rem;
        }

        @include m.tablet{
            width: 55rem;
            font-size: 6.2rem;
        }

        @include m.escritorio{
            width: 70%;
            font-size: 8.6rem;
        }

        @include m.escritorioXL{
            width: 70%;
            font-size: 9.8rem;
        }

        @include m.escritorioXLL{
            width: 55%;
            font-size: 11rem;
        }
    }
}

.webp .header{
    background-image: url('../img/desktop/image-hero.webp');
} 

.no-webp .header{
    background-image: url('../img/desktop/image-hero.jpg');
}


.navegacion{
    padding-top: 2rem;
    height: 8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .menu{
        width: 4rem;
        z-index: 6;

        &:hover{
            cursor: pointer;
        }

        @include m.tablet {
            display: none;
        }
    }

    .logo{
        width: 20rem;
        height: auto;
        z-index: 7;
    }

    .enlaces-header{
        position: fixed;
        left: 0;
        top: 0;
        display: flex;
        width: 100%;
        height: 100vh;
        background: v.$negro;
        z-index: 5;
        flex-direction: column;
        justify-content: space-around;
        padding: 15rem 5%;
        clip-path: circle(0% at 100% 0);
        transition: all .5s ease;

        a{
            font-family: v.$Josefin;
            font-size: 4.2rem;  
            color: v.$blanco;
            @include m.bordeMenu(v.$blanco, 50%); 
            
            @include m.tablet{
                font-size: 1.6rem;
                display: inline-block;
                margin-right: 2rem;
                @include m.borde(v.$blanco, 100%, 0);
                font-family: v.$Alata;
            }
        }


        @include m.tablet {
            background: none;
            clip-path: none;
            width: auto;
            height: auto;
            display: inline-block;
            padding: 0;
            position: relative;
            
        }
    }

    .abrir{
        clip-path: circle(141.2% at 100% 0);       
    }
}


